import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Table,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
import {BBC, BC} from 'Mock'
const ORDER_LIST = 'wms/outWarehouseList'

@connect(
  state => ({...state.outWarehouseListSell}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns2 = [{
      title: '序号',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '商品名称',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '商品条码',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '单位',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '计划数量',
      dataIndex: 'planQty',
      key: 'planQty',
      align: 'center'
    }, {
      title: '出库数量',
      dataIndex: 'distributeQty',
      key: 'distributeQty',
      align: 'center'
    }, {
      title: '是否套档',
      dataIndex: 'kitFlagDesc',
      key: 'kitFlagDesc',
      align: 'center'
    }, {
      title: '加工备注',
      dataIndex: 'handleRemark',
      key: 'handleRemark',
      align: 'center'
    }, {
      title: '规格备注',
      dataIndex: 'specsRemark',
      key: 'specsRemark',
      align: 'center'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData(type) {
    try {
      let searchParams = {}
      if (hasVariant(ORDER_LIST)) {
        searchParams = JSON.parse(getVariant(ORDER_LIST))
      } else {
        removeVariant(ORDER_LIST)
      }
      const {handleGetDetailPrint2} = this.props
      handleGetDetailPrint2({searchParams})
    } catch (err) {
      console.error(err)
    }
  }
  /* 关闭窗口 */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /* 打印 */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
    // try {
    //   let ids = [],
    //     reqData = []
    //   const {match: {params: {id}}} = this.props
    //   if (hasVariant(ORDER_LIST)) {
    //     ids = getVariant(ORDER_LIST)
    //   } else {
    //     removeVariant(ORDER_LIST)
    //   }
    //   if (!ids.length) return (0, this._closeWindow)(message.error('更新打印次数失败，订单编号错误'))
    //   ids.map((o) => {
    //     reqData.push({
    //       waveId: o
    //     })
    //   })
    //   const {handleUpdatePrintNum} = this.props
    //   handleUpdatePrintNum({data: reqData}, id)
    // } catch (err) {
    //   console.error(err)
    // }
  }

  // 画条形码
  componentDidUpdate() {
    const {printData} = this.props
    printData.map((o) => {
      o.outCode && JsBarcode(`#key${o.outCode}`, o.outCode, {
        width: 1.5,
        height: 30,
        fontSize: 12
        // displayValue: false
      })
      o.waveCode && JsBarcode(`#key${o.waveCode}`, o.waveCode, {
        width: 1.5,
        height: 30,
        fontSize: 12
        // displayValue: false
      })
    })
  }
  render() {
    const {printData, tableLoading, printLoading} = this.props

    return (
      <React.Fragment>
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button> : (printLoading ? <h2 style={{textAlign: 'center'}}>正在加载打印数据</h2> : <h2 style={{textAlign: 'center'}}>暂无打印数据</h2>)
        }
        {
          printData.map((o) => <section className="outboundOrderTemplate PageBreakAfter" key={o.outCode}>
              <div style={{position: 'relative', height: '60px', borderBottom: '1px solid #000', marginTop: '30px'}}>
                <h2 className="title">汇总分拣单打印</h2>
                <img id={`key${o.outCode}`} style={{position: 'absolute', top: '-5px', left: '30px'}} />
                <img id={`key${o.waveCode}`} style={{position: 'absolute', top: '-5px', right: '30px'}} />
                {/* <div style={{position: "absolute", top: '10px', right: '10px', fontSize: '12px'}}>
                  <p style={{margin: '0px'}}>打印日期：{o.printTime}</p>
                  <p style={{margin: '0px'}}>波次号：{o.waveCode}</p>
                </div> */}
              </div>
              <div className='header'>
                  <Row type="flex" justify="start">
                      <Col span={8} className='delBox'>
                          <label>出库单：</label>
                          <span>{o.outCode || ''}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>波次号：</label>
                          <span>{o.waveCode || ''}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>业务类型：</label>
                          <span>{`${o.businessType || ''}-${o.businessTypeDesc || ''}`}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>商品种类：</label>
                          <span>{ o.productCategoryCount || ''}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>配送门店：</label>
                          <span>{`${o.shopCode || ''}-${o.shopName || ''}`}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>站点：</label>
                          <span>{`${o.stationCode || ''}-${o.stationName || ''}`}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>配送客户：</label>
                          <span>{`${o.customerCode || ''}-${o.customerName || ''}`}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>收货地点：</label>
                          <span>{ o.deliveryAddress || ''}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>出库地点：</label>
                          <span>{`${o.sendLocationCode || ''}-${o.sendLocationName || ''}`}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>配送地址：</label>
                          <span>{ o.deliveryAddress || ''}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>打印日期：</label>
                          <span>{ o.printTime || ''}</span>
                      </Col>
                      <Col span={8} className='delBox'>
                          <label>发货区域：</label>
                          <span>{ o.pickingSortNumber}</span>
                      </Col>
                  </Row>
              </div>
              <div className='foot' style={{marginBottom: '10px'}}>
                <Table
                  rowKey={(record, index) => index}
                  loading={tableLoading}
                  columns={this.columns2}
                  dataSource={o.detailList}
                  pagination={false}
                  style={{marginTop: '6px'}}
                  bordered
                  size="middle"
                />
              </div>
            </section>)
        }
        <div className="printLoadingSty">
          <Spin spinning={printLoading} size="large"/>
        </div>
      </React.Fragment>
    )
  }
}



